import { OverlayPanel, OverlayPanelProps } from "primereact/overlaypanel";
import React from "react";

interface ISimpleOverlayPanelProps extends OverlayPanelProps {
  content?: any;
  ref?: any;
}

const SimpleOverlayPanelWithoutMemo: React.FC<ISimpleOverlayPanelProps> = React.forwardRef(
  (props: ISimpleOverlayPanelProps, ref: any) => {
    const { className, content, ...primeReactProps } = props;

    return (
      <React.Fragment>
        <OverlayPanel
          ref={ref}
          className={`bg-white rounded-md w-full shadow-overlayPanel  ${className}`}
          {...primeReactProps}
        >
          {content}
        </OverlayPanel>
      </React.Fragment>
    );
  }
);

const SimpleOverlayPanel = React.memo(SimpleOverlayPanelWithoutMemo);

// ----------- exports ------------------------ //
export default React.memo(SimpleOverlayPanel);
